import { createStore } from 'redux'

const initialState = {
  sidebarShow: false,
}

// @ts-ignore
function changeState(state = initialState, { type, ...rest }): { sidebarShow: boolean } {
  switch (type) {
    case 'set':
      return { ...state, ...rest }
    default:
      return state
  }
}

const store = createStore(changeState)
export default store
